/* checkinf */
.scrollTop {
    position: fixed;
    width: 60px;
    bottom: 20px;
    align-items: center;
    height: 20px;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    color: white;
    left: 95%;
    margin-bottom: 1%;
}

.scrollTop:hover {
    opacity: 1;
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.5;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
